.nav {
  position: relative;
}

.nav__button {
  border: none;
  background-color: inherit;
  cursor: pointer;
  padding: 5px;

  &:hover {
    background-color: #ebebeb;
  }

  &:active {
    opacity: 30%;
  }

  &:focus {
    outline: 2px solid #68b738;
  }
}

.nav__container {
  position: absolute;
  bottom: -224px;
  right: -20px;
}

.nav__list {
  position: relative;
  display: block;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  z-index: 2;
}

.nav__item {
  display: flex;
  position: relative;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;
  border-bottom: 1px solid #e6e6e6;
  margin: 0;
  background-color: #ffffff;
  height: 66px;
  width: 320px;

  &:first-child {
    border-top: 1px solid #e6e6e6;
  }
}

.nav__link {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #000000;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 20px;
  padding: 10px 0;
  align-self: center;

  &--current::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color: #68b738;
    bottom: 0;
    right: 0;
  }

  &:hover {
    opacity: 60%;
  }

  &:active {
    opacity: 30%;
  }

  &:focus {
    outline: 2px solid #68b738;
    outline-offset: 2px;
  }
}

@media (min-width: $tablet-width) {
  .nav__container {
    position: static;
  }

  .nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 440px;
  }

  .nav__item {
    width: auto;
    height: auto;
    border: none;

    &:first-child {
      border: none;
    }
  }

  .nav__link {
    font-size: 18px;
    line-height: 24px;
  }

  .nav__button {
    display: none;
  }
}

